import * as React from 'react';
import { useState } from 'react';
import { ThemeProvider } from '@material-ui/styles';
import { createMuiTheme, makeStyles } from '@material-ui/core/styles';
import { Button, CssBaseline, TextField, Typography, Card, Box, Container } from '@material-ui/core';
import { useSnackbar } from 'notistack';
import { login } from './auth.js';
import { useHistory } from "react-router-dom"

const myTheme = createMuiTheme({
  palette: {
      primary: {
          light: '#58a5f0',
          main: '#0277bd',
          dark: '#58a5f0',
          contrastText: '#fff',
      }
  }
})

function LoginPage(theme) {

    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const history = useHistory();
    const submit = e => {
      e.preventDefault();
      enqueueSnackbar('로그인 요청 중...')
      login({email, password})
      .then(()=> {
        sessionStorage.setItem('userId', email);
        history.push("/");
        closeSnackbar();
      })
      .catch(()=> 
        enqueueSnackbar('아이디 또는 비밀번호가 올바르지 않습니다', {variant: 'error',})
      );
    }

    const useStyles = makeStyles((theme) => ({
        
        paper: {
          marginTop: theme.spacing(8),
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        },
        title: {
          marginBottom: 30,
        },
        logincard: {
          marginTop: 120,
        },
        form: {
          color: 'black',
          width: '80%', // Fix IE 11 issue.
          marginTop: theme.spacing(1),
        },
        submit: {
          margin: theme.spacing(3, 0, 5),
        },
    }));

    const classes = useStyles();

    return (
      <ThemeProvider theme={myTheme}>
        <Container className={classes.main} maxWidth="xs">
            <CssBaseline />
            <Card className={classes.logincard}>
            <div className={classes.paper}>
                <Typography variant="h4" noWrap className={classes.title}>
                  <Box fontWeight="fontWeightBold" m={1}>
                  Kant | Admin
                  </Box>
                </Typography>
                <form className={classes.form} onSubmit={submit} noValidate>
                <TextField
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    id="id"
                    label="아이디"
                    name="id"
                    autoFocus
                    onChange={e=> setEmail(e.target.value)}
                />
                <TextField
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    name="password"
                    label="비밀번호"
                    type="password"
                    id="password"
                    onChange={e=> setPassword(e.target.value)}
                />
                
                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    disabled={(email === '') || (password === '') }
                >
                    로그인
                </Button>
                
                </form>
            </div>
            </Card>            
            </Container>
            </ThemeProvider>
        )
    }

export default LoginPage;