import * as React from 'react';
import { useState, useEffect } from 'react';
import axios from 'axios';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { Button, Grid, IconButton, Paper } from '@material-ui/core'
import CachedIcon from '@material-ui/icons/Cached';
import { useSnackbar } from 'notistack';


import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { useHistory } from 'react-router';


export default function Uploader(props) {
    const history = useHistory();
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    const [tempFile, setTempFile] = useState(null);

    const handleFile = (e) => {
        e.preventDefault();
        setTempFile(e.target.files[0]);
    }
    const submitFile = (e) => {
        e.preventDefault();
        if (tempFile !== null) {
            props.getFile(tempFile)
            history.push('/inspect')
        } else {
            enqueueSnackbar('파일이 선택되지 않았습니다', {variant: 'error'})
        }
    }

    return (
        <>
        <div style={{display:'flex', marginBottom:10}}>
            <Typography variant="h5" noWrap >
            <Box fontWeight="fontWeightBold" m={1}>
                업로드
            </Box>
            </Typography>
            {/* page navigation button
            <IconButton><ArrowBackIosIcon fontSize="small"/></IconButton>
            <Typography variant="h7" style={{marginTop:14}}>1 페이지</Typography>
            <IconButton><ArrowForwardIosIcon fontSize="small"/></IconButton> */}
            <div style={{marginLeft:'auto', paddingTop:5}}>
                
            </div>
        </div>
        <div style={{ height: '100%', width: '100%', marginTop:20, marginLeft:10}}>
            <div style={{display: 'flex', gap: '20px'}}>
                <Typography style={{ marginTop:5 }}>업로드 파일 :</Typography>
                <Button variant="outlined" component="label"><input type="file" accept="application/pdf" onChange={handleFile} /></Button> 
                <Button variant="outlined" color="primary" onClick={submitFile}><CachedIcon fontSize='small'/>&nbsp;&nbsp;실행</Button> 
            </div>
        </div>
        </>
    );
}